.tiles {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    width: 100%;
    max-width: 70%;
    margin: 0 auto;
  }
  
  .tile {
    position: relative;
    background: black;
    overflow: hidden;
    aspect-ratio: 0.5;
  }
  
  .tile-media {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
  }
  
  .tile-info {
    position: absolute;
    bottom: 0;
    width: 100%;
    background: rgba(0, 0, 0, 0.7);
    color: white;
    text-align: center;
    padding: 10px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .chip-set {
    display: flex;
    justify-content: center;
    gap: 5px;
  }
  
  .chip {
    padding: 0 5px;
    height: 20px;
    background-color: #555;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
    font-size: 10px;
  }
  
  .two-sided-chip {
    height: 20px;
    display: inline-flex;
    border-radius: 4px;
    overflow: hidden;
    font-size: 10px;
  }
  
  .two-sided-chip .left-part {
    background-color: #555;
    padding: 0 5px;
    display: flex;
    align-items: center;
  }
  
  .two-sided-chip .right-part {
    background-color: #888;
    padding: 0 5px;
    display: flex;
    align-items: center;
  }
  
  .split-chip {
    height: 20px;
    background-color: #888;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
    font-size: 10px;
  }
  
  .split-chip span {
    padding: 0 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-left: 1px solid #555;
    height: 100%;
  }
  
  .split-chip span:first-child {
    border-left: none; /* Remove the border from the first element */
  }
  
  .green-chip {
    background-color: rgb(49, 97, 49);
  }
  
  .tile-dropdown select {
    background: rgb(49, 97, 49);
    color: white;
    border: none;
    border-radius: 4px;
    font-size: 10px;
    appearance: none;
    cursor: pointer;
  }  